import React, { Component } from "react";
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { ProductConsumer } from '../context';
import PropTypes from 'prop-types';
class Product extends Component {

  render() {
      const { id, title, img, price, inCart } = this.props.product;
      return (
        <ProductWrapper className="col-9 col-md-6 col-lg-4 mx-auto my-3">
            <div className="card">
              <ProductConsumer>
            {value => (
              <div className="img-container p-2"
              onClick = {() => value.handleDetail(id)}
              >
                <Link to="/Shopping-Cart/details">
                  <img src={img} width="200px" height="350px" alt={title} className="card-img-top" />  
                </Link>
                <button className="cart-btn" disabled={inCart ? true : false}
                onClick = {() => {
                  value.addToCart(id);
                  value.openModal(id);
                }
                } >
                  {inCart ? (
                    <p className="mb-0" disabled>In cart</p>
                  ) : <i className="fa fa-shopping-cart"> Add to Cart</i>}
                </button>
              </div>
            )}
            </ProductConsumer>
            <div className="card-footer product-info"> 
            <p className="mb-0 text-title">{title}</p>
            <p className="mb-0 font-italic text-title ">$ {price}</p>
            </div>
            </div>
        </ProductWrapper>
      );
              
}
}
Product.propTypes = {
  product: PropTypes.shape({
    id: PropTypes.number,
    img: PropTypes.string,
    title: PropTypes.string,
    price: PropTypes.number,
    inCart: PropTypes.bool
  }).isRequired
};
const ProductWrapper = styled.div`
.card {
  border-color: transparent;
  transition: all 1s linear;
}
.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s linear;
}
.product-info {
  display: flex;
  justify-content: space-between;
}
&:hover {
  .card{  
    border: 0.04rem solid rgba(0, 0, 0, 0.2);
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.2);
  }
  .card-footer {
    background: rgba(247, 247, 247);
  }
}
.img-container {
  position: relative;
  overflow: hidden;
}
.card-img-top {
  transition: all 0.6s linear;
}
.img-container:hover .card-img-top {
  transform: scale(1.2);
}
.cart-btn {
  position: absolute;
  bottom: 0;
  right: 0;
  background: var(--lightBlue);
  color: var(--mainWhite);
  border: none;
  font-size: 1.4rem;
  border-radius: 0.5rem 0 0 0;
  transition: all 0.5s linear;
  transform: translate(100%, 100%);
}
.img-container:hover .cart-btn {
  transform: translate(0%, 0%);
}
.cart-btn:hover {
  color: var(--mainBlue);
  cursor: pointer;
}
`

export default Product;
